.site-header {
	padding: 4rem 0;
}

.site-header .site-header__title {
	margin-bottom: 2rem;
}

.site-footer__credit {
	font-size: 12px;
	color: #adb5bd;
	text-transform: uppercase;
	letter-spacing: 1px;
	padding: .5rem;
}

.site-nav ul {
	display: flex;
	flex-wrap: nowrap;
	list-style: none;
	margin: 0;
	padding: 0;
}

.site-nav ul li {
	padding: .5rem;
	margin-left: -0.5rem;
	margin-right: 1.5rem;
	font-size: 12px;
	text-transform: uppercase;
	letter-spacing: 1px;
}

.site-nav ul li a {
	transition: all 0.2s ease-in-out;
}

.site-nav ul li a:link, .site-nav ul li a:visited {
	color: #adb5bd;
	text-decoration: none;
}

.site-nav ul li a:hover {
	color: #212529;
}

.site-nav ul li.current-menu-item a:link, .site-nav ul li.current-menu-item a:visited {
	color: #0d4ef3;
}

.site-nav ul li.current-menu-item a:hover {
	color: #0a43d2;
}

.site-sub-nav {
	display: flex;
}

.site-sub-nav li {
	list-style: none;
	margin-right: 1rem;
	font-size: 12px;
}

.site-sub-nav li.current_page_item a:link, .site-sub-nav li.current_page_item a:visited {
	color: #0d4ef3;
}

.site-sub-nav li a:link, .site-sub-nav li a:visited {
	color: #adb5bd;
}

.post-container {
	margin-bottom: 4rem;
}

.post-info {
	margin-bottom: 1rem;
	font-size: 13px;
	line-height: 1rem;
	color: #6c757d;
	padding-bottom: .25rem;
	border-bottom: 1px dashed #e9ecef;
}

.post-info .post-info__time {
	color: #ced4da;
}

.featured-image__container {
	max-width: 100%;
	position: relative;
	margin-bottom: 1rem;
	border: 2px solid #e9ecef;
	border-radius: 4px;
	padding: 1rem;
	box-shadow: 0 0 16px rgba(0, 0, 0, 0.09);
}

.featured-image__container img {
	max-width: 100%;
	height: auto;
}

.single-post__title {
	font-size: 4rem;
}

.page-container {
	margin-bottom: 4rem;
}

.page-container .info-box {
	height: 120px;
	background-color: pink;
}

.archive-title {
	font-size: .75rem;
	text-transform: uppercase;
	letter-spacing: 1px;
	font-weight: 600;
	margin-bottom: 3rem;
}

.archive-title .archive-title__bage {
	padding: .25rem 1rem;
	background-color: #e9ecef;
}

body {
	font-family: 'Montserrat', sans-serif;
}
